body {
  background:#f3f3f3;
}



.Carousel {
  width: 100%;
  height: 100%;
  margin-bottom: 2.5rem;
}

a {
  color: white;
}

button:focus {
  outline: none;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #3c8dbc;
  height: 50px;
  padding: 30px;
  color: white;
}
.App-footer {
  background-color: #3c8dbc;
  height: 120px;
  padding: 5px;
  color: white;
  margin-top: 2.5rem;
}
.a{
  color: white !important;
  size: 14px !important;
}
a:hover {
}

.App-title {
  font-size: 1.5em;
}

.recipe_buttons {
  border: solid 2px #e74c3c;
  background: transparent;
  padding: 0.4rem 0.6rem;
  margin: 0 0.2rem;
  border-radius: 1px;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 0.7rem;
}

.trails__box {
  background: #fff;
  padding-bottom: 2rem;
  border-radius: 5%;
  opacity: 1;
  box-shadow: -1px 2px 34px -13px rgba(0,0,0,0.75);
  text-align: left;
  overflow: hidden;
}
.trails__box:hover {
  background: #E0E0E0;
  opacity: .5;

}
.trail__text {
  margin-left: 15px;
}

.trail__title {
  color: #EF5350;
  text-transform: uppercase;
  font-size: 1.7rem;
  padding: 1.8rem 0 0.2rem 0;
}

.trail__subtitle {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.3rem;
  letter-spacing: 2px;
}



.form__input {
  background: transparent;
  border: 0;
  border-bottom: solid 2px #2c3e50;
  margin-right: 10px;
  letter-spacing: 2px;
  width: 10%;
  padding: 0.4rem;
}

.form__button {
  border: solid 2px #e74c3c;
  background: transparent;
  padding: 0.2rem 0.9rem;
  margin: 0 0.2rem;
  border-radius: 1px;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 0.7rem;
}

input[type="text"] { 
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #f3f3f3 inset;
  -webkit-text-fill-color: #2c3e50 !important;
}


.active-trail {
  margin: 0 auto;
  width: 60%;
}

.active-trail__img {
  height: 500px !important;
}

.active-trail__title {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 1rem 0;
}

.active-trail__publisher, .active-trail__website {
  text-transform: uppercase;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.active-trail__publisher span {
  text-transform: none;
  font-weight: 300;
  letter-spacing: 2px;
  margin-left: 5px;
}

.active-trail__website span {
  text-transform: lowercase;
  font-weight: 300;
  letter-spacing: 2px;
  color: #e74c3c;
  margin-left: 10px;
}

.active-trail__button {
  border: solid 2px #e74c3c;
  background: transparent;
  padding: 0.2rem 0.9rem;
  margin: 0 0.2rem;
  border-radius: 1px;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
}
